.todo-item{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px, 20px, 0;
    width: 70%;
    border-bottom: 1px solid #c7c7c7;
    font-family: "Roboto", sans-serif;
    font-weight: 100;
    font-size: 16px;
    color: #333333;
}

input[type=checkbox] {
    margin-right: 30px;
    font-size: 20px;
}

input[type=checkbox]:focus {
    outline: 0;
}
.strike{
    text-decoration:line-through;
    color: brown;
}

.todo-list{
    background-color: white;
    margin: 2%;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #dfdede;
    border-radius: 1%;
    box-shadow: 
        /* Top layer */
        0 1px 1px rgba(0,0,0,30),
        /* Top layer shadow */
        0 10px 0 -5px rgb(139, 139, 139),
        /* second layer */
        0 10px 1px -4px rgba(0,0,0,30),
        /* Second layer shadow*/
        0 20px 0 -10px rgb(90, 90, 90),
        /* Second layer shadow */
        0 20px 1px -9px rgba(0,0,0,30);
    padding: 30px;
}

/* If the screen size is wide or more, set the font-size of <div> to */
@media screen and (min-width: 751px) {
    .todo-list{
        background-color: white;
